"use client"

import React, { useRef, useEffect, useState, useCallback } from 'react'

type Direction = 'up' | 'down' | 'left' | 'right'

interface Entity {
  x: number
  y: number
  direction: Direction
  speed: number
}

interface Position {
  x: number
  y: number
}

const CELL_SIZE = 20
const CANVAS_WIDTH = 600
const CANVAS_HEIGHT = 400
const MAZE_WIDTH = CANVAS_WIDTH / CELL_SIZE
const MAZE_HEIGHT = CANVAS_HEIGHT / CELL_SIZE

// Simple maze layout: 1 = wall, 0 = pellet, 2 = empty space, 3 = power pellet
const createMaze = (): number[][] => {
  const maze: number[][] = []
  
  // Create border walls and fill with pellets
  for (let row = 0; row < MAZE_HEIGHT; row++) {
    maze[row] = []
    for (let col = 0; col < MAZE_WIDTH; col++) {
      if (row === 0 || row === MAZE_HEIGHT - 1 || col === 0 || col === MAZE_WIDTH - 1) {
        maze[row][col] = 1 // Wall
      } else if ((row === 2 || row === MAZE_HEIGHT - 3) && (col === 2 || col === MAZE_WIDTH - 3)) {
        maze[row][col] = 3 // Power pellet
      } else if (row % 4 === 0 && col % 6 === 0 && row > 2 && row < MAZE_HEIGHT - 3) {
        maze[row][col] = 1 // Internal walls
      } else {
        maze[row][col] = 0 // Pellet
      }
    }
  }
  
  // Create some paths
  for (let row = 1; row < MAZE_HEIGHT - 1; row++) {
    for (let col = 1; col < MAZE_WIDTH - 1; col++) {
      if (row === Math.floor(MAZE_HEIGHT / 2)) {
        maze[row][col] = maze[row][col] === 1 ? 1 : 0 // Horizontal corridor
      }
      if (col === Math.floor(MAZE_WIDTH / 2)) {
        maze[row][col] = maze[row][col] === 1 ? 1 : 0 // Vertical corridor
      }
    }
  }
  
  return maze
}

const PacmanGame: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null)
  const animationFrameId = useRef<number>()
  const [score, setScore] = useState<number>(0)
  const [gameOver, setGameOver] = useState<boolean>(false)
  const [gameWon, setGameWon] = useState<boolean>(false)
  const [maze, setMaze] = useState<number[][]>(createMaze())
  const [pelletCount, setPelletCount] = useState<number>(0)

  // Game entities
  const [pacman, setPacman] = useState<Entity>({
    x: CELL_SIZE * 1.5,
    y: CELL_SIZE * 1.5,
    direction: 'right',
    speed: 2,
  })

  const [ghosts, setGhosts] = useState<Entity[]>([
    {
      x: CELL_SIZE * (MAZE_WIDTH - 2.5),
      y: CELL_SIZE * 1.5,
      direction: 'left',
      speed: 1.5,
    },
    {
      x: CELL_SIZE * 1.5,
      y: CELL_SIZE * (MAZE_HEIGHT - 2.5),
      direction: 'up',
      speed: 1.2,
    }
  ])

  // Count initial pellets
  useEffect(() => {
    const count = maze.flat().filter(cell => cell === 0 || cell === 3).length
    setPelletCount(count)
  }, [maze])

  // Handle keyboard controls
  const handleKeyDown = useCallback((e: KeyboardEvent) => {
    if (gameOver || gameWon) return
    
    let newDirection: Direction | null = null
    switch (e.key) {
      case 'ArrowUp':
        newDirection = 'up'
        break
      case 'ArrowDown':
        newDirection = 'down'
        break
      case 'ArrowLeft':
        newDirection = 'left'
        break
      case 'ArrowRight':
        newDirection = 'right'
        break
    }

    if (newDirection) {
      setPacman((prev) => ({ ...prev, direction: newDirection }))
    }
  }, [gameOver, gameWon])

  // Check if position is valid (not a wall)
  const isValidPosition = (x: number, y: number): boolean => {
    const col = Math.floor(x / CELL_SIZE)
    const row = Math.floor(y / CELL_SIZE)
    
    if (row < 0 || row >= MAZE_HEIGHT || col < 0 || col >= MAZE_WIDTH) {
      return false
    }
    
    return maze[row][col] !== 1
  }

  // Update entity position
  const updateEntityPosition = (entity: Entity): Entity => {
    let { x, y, direction, speed } = entity
    let newX = x
    let newY = y

    switch (direction) {
      case 'up':
        newY = y - speed
        break
      case 'down':
        newY = y + speed
        break
      case 'left':
        newX = x - speed
        break
      case 'right':
        newX = x + speed
        break
    }

    // Check boundaries and walls
    if (isValidPosition(newX, newY)) {
      return { ...entity, x: newX, y: newY }
    }

    return entity
  }

  // Random direction for ghosts
  const getRandomDirection = (): Direction => {
    const directions: Direction[] = ['up', 'down', 'left', 'right']
    return directions[Math.floor(Math.random() * directions.length)]
  }

  // Update ghost with simple AI
  const updateGhost = (ghost: Entity): Entity => {
    const updatedGhost = updateEntityPosition(ghost)
    
    // If ghost didn't move (hit wall), change direction
    if (updatedGhost.x === ghost.x && updatedGhost.y === ghost.y) {
      return { ...ghost, direction: getRandomDirection() }
    }
    
    // Randomly change direction sometimes
    if (Math.random() < 0.02) {
      return { ...updatedGhost, direction: getRandomDirection() }
    }
    
    return updatedGhost
  }

  // Check collision between two entities
  const checkCollision = (entity1: Entity, entity2: Entity): boolean => {
    const distance = Math.hypot(entity1.x - entity2.x, entity1.y - entity2.y)
    return distance < CELL_SIZE * 0.8
  }

  // Collect pellets
  const collectPellet = (pacmanPos: Entity) => {
    const col = Math.floor(pacmanPos.x / CELL_SIZE)
    const row = Math.floor(pacmanPos.y / CELL_SIZE)
    
    if (row >= 0 && row < MAZE_HEIGHT && col >= 0 && col < MAZE_WIDTH) {
      if (maze[row][col] === 0) {
        // Regular pellet
        setMaze(prev => {
          const newMaze = [...prev]
          newMaze[row] = [...newMaze[row]]
          newMaze[row][col] = 2 // Empty space
          return newMaze
        })
        setScore(prev => prev + 10)
        setPelletCount(prev => prev - 1)
      } else if (maze[row][col] === 3) {
        // Power pellet
        setMaze(prev => {
          const newMaze = [...prev]
          newMaze[row] = [...newMaze[row]]
          newMaze[row][col] = 2 // Empty space
          return newMaze
        })
        setScore(prev => prev + 50)
        setPelletCount(prev => prev - 1)
      }
    }
  }

  // Game loop
  const gameLoop = useCallback(() => {
    if (gameOver || gameWon) return

    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext('2d')
    if (!ctx) return

    // Clear canvas
    ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT)

    // Draw maze
    maze.forEach((row, rowIndex) => {
      row.forEach((cell, colIndex) => {
        const x = colIndex * CELL_SIZE
        const y = rowIndex * CELL_SIZE

        switch (cell) {
          case 1: // Wall
            ctx.fillStyle = '#1e40af'
            ctx.fillRect(x, y, CELL_SIZE, CELL_SIZE)
            break
          case 0: // Pellet
            ctx.fillStyle = '#fbbf24'
            ctx.beginPath()
            ctx.arc(x + CELL_SIZE / 2, y + CELL_SIZE / 2, 2, 0, Math.PI * 2)
            ctx.fill()
            break
          case 3: // Power pellet
            ctx.fillStyle = '#fbbf24'
            ctx.beginPath()
            ctx.arc(x + CELL_SIZE / 2, y + CELL_SIZE / 2, 6, 0, Math.PI * 2)
            ctx.fill()
            break
        }
      })
    })

    // Update and draw Pacman
    const newPacman = updateEntityPosition(pacman)
    setPacman(newPacman)
    collectPellet(newPacman)

    // Draw Pacman
    ctx.fillStyle = '#facc15'
    ctx.beginPath()
    
    // Calculate mouth angle based on direction
    let startAngle = 0.2 * Math.PI
    let endAngle = 1.8 * Math.PI
    
    switch (newPacman.direction) {
      case 'right':
        startAngle = 0.2 * Math.PI
        endAngle = 1.8 * Math.PI
        break
      case 'left':
        startAngle = 1.2 * Math.PI
        endAngle = 0.8 * Math.PI
        break
      case 'up':
        startAngle = 1.7 * Math.PI
        endAngle = 1.3 * Math.PI
        break
      case 'down':
        startAngle = 0.7 * Math.PI
        endAngle = 0.3 * Math.PI
        break
    }
    
    ctx.arc(newPacman.x, newPacman.y, CELL_SIZE / 2 - 2, startAngle, endAngle)
    ctx.lineTo(newPacman.x, newPacman.y)
    ctx.fill()

    // Update and draw ghosts
    const newGhosts = ghosts.map(ghost => updateGhost(ghost))
    setGhosts(newGhosts)

    newGhosts.forEach((ghost, index) => {
      // Draw ghost
      const colors = ['#ef4444', '#8b5cf6', '#06b6d4', '#f59e0b']
      ctx.fillStyle = colors[index % colors.length]
      
      // Ghost body (circle)
      ctx.beginPath()
      ctx.arc(ghost.x, ghost.y - 2, CELL_SIZE / 2 - 2, Math.PI, 0)
      ctx.fill()
      
      // Ghost bottom (wavy)
      ctx.fillRect(ghost.x - CELL_SIZE / 2 + 2, ghost.y - 2, CELL_SIZE - 4, CELL_SIZE / 2)
      
      // Ghost eyes
      ctx.fillStyle = 'white'
      ctx.beginPath()
      ctx.arc(ghost.x - 4, ghost.y - 6, 2, 0, Math.PI * 2)
      ctx.arc(ghost.x + 4, ghost.y - 6, 2, 0, Math.PI * 2)
      ctx.fill()
      
      ctx.fillStyle = 'black'
      ctx.beginPath()
      ctx.arc(ghost.x - 4, ghost.y - 6, 1, 0, Math.PI * 2)
      ctx.arc(ghost.x + 4, ghost.y - 6, 1, 0, Math.PI * 2)
      ctx.fill()

      // Check collision with Pacman
      if (checkCollision(newPacman, ghost)) {
        setGameOver(true)
        return
      }
    })

    // Check win condition
    if (pelletCount <= 0) {
      setGameWon(true)
      return
    }

    // Continue game loop
    animationFrameId.current = requestAnimationFrame(gameLoop)
  }, [pacman, ghosts, maze, pelletCount, gameOver, gameWon])

  // Setup game loop and event listeners
  useEffect(() => {
    window.addEventListener('keydown', handleKeyDown)
    animationFrameId.current = requestAnimationFrame(gameLoop)

    return () => {
      window.removeEventListener('keydown', handleKeyDown)
      if (animationFrameId.current) {
        cancelAnimationFrame(animationFrameId.current)
      }
    }
  }, [handleKeyDown, gameLoop])

  // Reset game
  const resetGame = () => {
    setScore(0)
    setGameOver(false)
    setGameWon(false)
    const newMaze = createMaze()
    setMaze(newMaze)
    const count = newMaze.flat().filter(cell => cell === 0 || cell === 3).length
    setPelletCount(count)
    
    setPacman({
      x: CELL_SIZE * 1.5,
      y: CELL_SIZE * 1.5,
      direction: 'right',
      speed: 2,
    })
    
    setGhosts([
      {
        x: CELL_SIZE * (MAZE_WIDTH - 2.5),
        y: CELL_SIZE * 1.5,
        direction: 'left',
        speed: 1.5,
      },
      {
        x: CELL_SIZE * 1.5,
        y: CELL_SIZE * (MAZE_HEIGHT - 2.5),
        direction: 'up',
        speed: 1.2,
      }
    ])
  }

  return (
    <div className="flex flex-col items-center space-y-4">
      <div className="flex items-center space-x-8 text-lg font-medium">
        <div>Score: {score}</div>
        <div>Pellets: {pelletCount}</div>
      </div>
      
      <div className="relative">
        <canvas
          ref={canvasRef}
          width={CANVAS_WIDTH}
          height={CANVAS_HEIGHT}
          className="border-2 border-border rounded-lg shadow-lg bg-black"
        />
        
        {(gameOver || gameWon) && (
          <div className="absolute inset-0 flex flex-col items-center justify-center bg-black bg-opacity-80 rounded-lg">
            <div className="text-center space-y-4">
              <div className="text-3xl font-bold text-white">
                {gameWon ? 'You Won!' : 'Game Over'}
              </div>
              <div className="text-lg text-gray-300">
                Final Score: {score}
              </div>
              <button
                className="px-6 py-3 rounded-lg bg-primary text-primary-foreground font-medium hover:bg-primary/90 transition-colors"
                onClick={resetGame}
              >
                Play Again
              </button>
            </div>
          </div>
        )}
      </div>
      
      <div className="flex space-x-4">
        <button
          className="px-4 py-2 rounded-lg bg-secondary text-secondary-foreground font-medium hover:bg-secondary/80 transition-colors"
          onClick={resetGame}
        >
          New Game
        </button>
      </div>
      
      <div className="text-sm text-muted-foreground text-center max-w-md">
        <p>Use arrow keys to move Pacman</p>
        <p>Collect all pellets while avoiding the ghosts!</p>
      </div>
    </div>
  )
}

export default PacmanGame
